.white {
  color: #f8fbfe !important;
}

.test {
  background-blend-mode: overlay;
  background-image: linear-gradient(
    135deg,
    #53a0fd 0%,
    #4161d4 100%
  ) !important;
}

.test1 {
  width: 300px;
  height: 300px;
}

